@font-face {
  font-family: "Buttonbar Font";
  src: url(./Jove.eot);
  /* IE9 Compat Modes */
  src: url(data:font/woff2;base64,) format('woff2'),  url(data:font/woff;base64,) format('woff'),  url(./Jove.ttf) format('truetype');
  /* Safari, Android, iOS */
}
.l-jove-theme .l-buttonbar-icon {
  font-family: "Buttonbar Font";
}
.l-jove-theme .l-buttonbar-icon.l-buttonbar-minimize-icon:before {
  content: "\0C01";
}
.l-jove-theme .l-buttonbar-icon.l-buttonbar-maximize-icon:before {
  content: "\0C02";
}
.l-jove-theme .l-buttonbar-icon.l-cross-icon:before {
  content: "\0C03";
}
.l-jove-theme .l-buttonbar-icon.l-buttonbar-print-icon:before {
  content: "\0C04";
}
.l-jove-theme .l-buttonbar-icon.l-buttonbar-reset-icon:before {
  content: "\0C05";
}
.l-jove-theme .l-buttonbar-icon.l-buttonbar-download-icon:before {
  content: "\0C06";
}
.l-jove-theme .l-buttonbar-icon.l-buttonbar-copy-icon:before {
  content: "\0C07";
}
.l-jove-theme .l-buttonbar-icon.l-buttonbar-upload-icon:before {
  content: "\0C08";
}
.l-jove-theme .l-buttonbar-icon.l-buttonbar-pdf-icon:before {
  content: "\0C09";
}
.l-jove-theme .l-buttonbar-icon.l-buttonbar-download-excel-icon:before {
  content: "\0C10";
}
