.item-player {
  width: auto;
}
.item-player.focused {
  box-shadow: 0px 0px 9px 3px #00000085;
}
.item-loader {
  background-image: url(data:image/svg+xml;base64,);
  height: 500px;
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: center;
  background-color: rgba(0, 0, 0, 0.1);
  margin: 0;
}
